.blogs {
  &__hero-section {
    height: 100vh;
    background-image: url(https://www2.0zz0.com/2022/04/29/05/990510922.png);
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    position: relative;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($dark, 0.4);
      z-index: 1;
    }

    &__container {
      position: relative;
      height: 100vh;
    }

    &__content {
      position: absolute;
      top: 45%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
      width: 100%;
    }

    &__scroll {
      position: absolute;
      left: 50%;
      transform: translateX(-50%) rotate(90deg);
      bottom: 4rem;
      z-index: 2;

      font-size: 3rem;
    }
  }

  &__recipe {
    padding-top: 15rem;
    padding-bottom: 15rem;
    &__container {
      display: grid;
      &.first {
        grid-template-columns: 30% 45%;
        justify-content: space-between;
        @include respond(sm) {
          grid-template-columns: repeat(1, 1fr);
          justify-content: center;
          gap: 4rem;
          margin-bottom: 4rem;
        }
      }
      &.second {
        grid-template-columns: 50% 35%;
        gap: 4rem;
        justify-content: flex-end;
        align-items: flex-end;
        transform: translateY(-15rem);
        position: relative;
        z-index: -1;
        @include respond(sm) {
          grid-template-columns: repeat(1, 1fr);
          transform: translateY(0rem);
        }
      }
      &.third {
        gap: 8rem;
        @include respond(sm) {
          margin-top: 4rem;
        }
      }
    }

    &__content {
      @include respond(sm) {
        order: -1;
        text-align: center;
      }

      &.first {
        @include respond(sm) {
          width: 50%;
          margin: auto;
        }
      }
      &.second {
        transform: translateY(-10rem);
        @include respond(sm) {
          transform: translateY(0rem);
        }
      }
      &.third {
        width: fit-content;
        margin: auto;
      }
    }
  }
}
